.display2 {
  font-weight: 600;
  font-size: 1.75em;
  line-height: 120%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #000000;
}
.swiper-button-next {
  z-index: 1;
  right: 0;
  &:after {
    display: none;
  }
}
.swiper-button-prev {
  z-index: 1;
  left: 0;
  &:after {
    display: none;
  }
}
.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: -2.8125em !important;
  &-bullet {
    width: 0.375em;
    height: 0.375em;
    background: #D9D9D9;
    opacity: 1;
    &-active {
      width: 0.75em;
      height: 0.75em;
      background: #B6E193;
    }
  }
}
.up {
  position: fixed;
  z-index: 4;
  right: 1.25em;
  bottom: 1.25em;
  width: 3.5em;
  height: 3.5em;
  background: #000000;
  border-radius: 0.1875em;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 1.5em;
  }
  .swiper-pagination {
    bottom: -1.5625em !important;
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 1.25em;
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    font-size: 0.9375em;
  }
}