.header {
  padding: 2.5em 0 0;
  &__menu-bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background: rgba(0, 0, 0, 0.32);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.header-top {
  padding-bottom: 0.75em;
  overflow: hidden;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__list {
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__link {
    height: 2.1875em;
    font-weight: 600;
    font-size: 0.9375em;
    line-height: 120%;
    color: #000000;
    border-radius: 0.25em;
    padding: 0 1.25em;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.5s;
    a {
      transition: unset;
    }
    &--active {
      background: #56BBF4;
      color: #FFFFFF;
    }
    &:hover {
      background: #56BBF4;
      color: #FFFFFF;
    }
  }
  &__items {
    margin-top: 2.1875em;
    display: none;
  }
  &__item {
    position: relative;
    &:not(:first-child) {
      margin-top: 0.9375em;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding-bottom: 15px;
      border-bottom: 1px solid #DDDDDD;
      &--active {
        i svg {
          transform: rotate(180deg);
        }
      }
      span {
        font-weight: 700;
        font-size: 0.875em;
        line-height: 120%;
        color: #000000;
      }
      i {
        flex-shrink: 0;
        margin-left: 0.625em;
        svg {
          transition: all 0.5s;
        }
      }
    }
    &-hidden {
      padding-top: 15px;
      display: none;
    }
    &-link {
      display: block;
      font-size: 0.875em;
      line-height: 120%;
      color: #000000;
      &:not(:first-child) {
        margin-top: 0.9375em;
      }
      &:hover {
        color: #56BBF4;
      }
    }
  }
  &__logo {
    display: block;
    width: 8.4375em;
    margin: 0 0.9375em;
    img {
      width: 100%;
    }
  }
  &__desc {
    text-align: right;
    margin-top: -1.875em;
  }
  &__subtitle {
    font-weight: 600;
    font-size: 0.75em;
    line-height: 100%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #000000;
  }
  &__location {
    font-size: 0.9375em;
    line-height: 120%;
    color: #000000;
    margin-top: 0.3125em;
  }
  &__info {
    font-size: 0.8125em;
    line-height: 100%;
    color: rgba(0, 0, 0, 0.48);
    margin-top: 0.3125em;
  }
  &__burger {
    width: 1.25em;
    height: 0.875em;
    position: relative;
    z-index: 7;
    cursor: pointer;
    margin-top: -2.5em;
    display: none;
    span {
      width: 100%;
      height: 0.125em;
      position: absolute;
      left: 0;
      background: #000000;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 0.375em;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 0.375em;
        transform: rotate(45deg);
      }
    }
  }
  &__search {
    width: 16em;
    height: 2.375em;
    position: relative;
    margin-top: 2.1875em;
    input {
      width: 100%;
      height: 100%;
      background: #F2F2F2;
      border-radius: 0.1875em;
      border: none;
      font-size: 0.875em;
      line-height: 120%;
      color: #000000;
      padding: 0 2.1875em 0 0.9375em;
    }
    button {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0.75em;
      transform: translate(0, -50%);
      background: transparent;
      padding: 0;
    }
  }
  &__tel {
    min-width: 182px;
    display: flex;
    align-items: center;
    height: 2.375em;
    background: linear-gradient(180deg, #F2F2F2 0%, rgba(242, 242, 242, 0.56) 100%);
    border-radius: 1.5em;
    padding: 0 2.625em 0 0.9375em;
    font-size: 0.9375em;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #000000;
    position: relative;
    margin-top: 0.75em;
    &:hover {
      background: linear-gradient(180deg, rgba(242, 242, 242, 0.56) 100% , #F2F2F2 0%);
    }
    span {
      position: absolute;
      top: 50%;
      right: 4px;
      transform: translate(0, -50%);
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
.header-bottom {
  border-top: 0.0625em solid #EDEDED;
  padding: 1.25em 0;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    li:not(:first-child) {
      margin-left: 2.5em;
    }
  }
  &__link {
    font-weight: 600;
    font-size: 0.8125em;
    line-height: 120%;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #000000;
    &:hover {
      color: #56BBF4;
    }
  }
  &__item {
    &--active {
      .header-bottom__item-show {
        color: #56BBF4;
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      font-weight: 600;
      font-size: 0.8125em;
      line-height: 120%;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #000000;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #56BBF4;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;
      width: 100%;
      background: #FFFFFF;
      padding-top: 2em;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-lists {
      width: calc(100% - 505px);
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      gap: 25px;
    }
    &-title {
      font-weight: 600;
      font-size: 0.8125em;
      line-height: 120%;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #000000;
      padding-bottom: 12px;
    }
    &-list ul {
      padding-top: 12px;
      border-top: 1px solid #EDEDED;
      li {
        font-size: 0.875em;
        line-height: 120%;
        color: #000000;
        &:hover {
          color: #56BBF4;
        }
      }
      li:not(:first-child) {
        margin: 0.9375em 0 0 0;
      }
    }
    &-img {
      width: 480px;
      margin-left: 25px;
      img {
        width: 100%;
      }
    }
  }
  &__hidden-inner {
    display: flex;
    padding: 2em 0 2em 0;
  }
}

@media screen and (max-width: 1200px) {
  .header-bottom {
    &__item-lists {
      width: 100%;
    }
    &__item-img {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__link {
      padding: 0 0.75em;
    }
  }
}

@media screen and (max-width: 900px) {
  .header {
    padding: 0.75em 0 0;
  }
  .header-top {
    padding-bottom: 0.4375em;
    &__inner {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
    &__left-wrapp {
      position: fixed;
      z-index: 4;
      top: 0;
      left: -105%;
      width: 22.5em;
      height: 100vh;
      background: #FFFFFF;
      padding: 5.625em 0.9375em 1.875em;
      transition: all 0.5s;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ededed;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #000000;
        border-radius: 3px;
      }
      &--active {
        left: 0;
      }
    }
    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 0.125em;
      margin-top: 0;
    }
    &__link {
      background: #F2F2F2;
      &--active {
        background: #56BBF4;
      }
    }
    &__items {
      display: block;
    }
    &__search {
      display: none;
    }
    &__logo {
      width: 5.875em;
    }
    &__tel {
      margin-top: 1.25em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -7px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 93.75em;
        height: 0.0625em;
        background: #ededed;
        pointer-events: none;
      }
      span svg {
        width: 28px;
        height: 28px;
      }
    }
    &__desc {
      margin-top: -2.5em;
    }
    &__subtitle {
      display: none;
    }
    &__location {
      font-size: 0.8125em;
      margin-top: 0;
    }
    &__info {
      display: none;
    }
    &__burger {
      display: block;
    }
  }
  .header-bottom {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 100%;
      background: #EDEDED;
    }
    &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      li:not(:first-child) {
        margin-left: 0;
      }
    }
    &__item-show {
      text-align: center;
      padding: 0 5px;
    }
    &__link {
      display: none;
      &2 {
        display: block;
        text-align: center;
        padding: 0 5px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__menu-bg {
      display: none;
    }
  }
  .header-top {
    &__left-wrapp {
      width: 100%;
    }
    &__location {
      span {
        display: none;
      }
    }
  }
  .header-bottom {
    &__item-lists {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 450px) {
  .header-top {
    &__desc {
      display: none;
    }
  }
}