.main {
  padding-bottom: 3.125em;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    background: linear-gradient(258.55deg, #BFE89D 0%, #A0C780 100%);
    border-radius: 0.75em;
    padding-top: 1.5625em;
  }
  &__left {
    max-width: 42.875em;
    padding:0 0 6em 6em;
  }
  &__right {
    width: calc(100% - 42.875em);
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: 0;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 31.25em;
      height: 31.25em;
      background: #E1F8CE;
      border-radius: 50%;
      filter: blur(120px);
    }
    img {
      position: relative;
      z-index: 1;
      display: block;
      width: 100%;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 4.5em;
    line-height: 120%;
    display: flex;
    align-items: flex-end;
    letter-spacing: -0.04em;
    color: #FFFFFF;
  }
}
.services {
  &__inner {
    padding: 3.125em 0 7.1875em;
    border-bottom: 1px solid #ededed;
  }
  &__title {
    text-align: center;
  }
  &__slider {
    position: relative;
    margin-top: 2.8125em;
    padding: 0 2.8125em;
  }
  &__slide {
    position: relative;
    &:hover {
      .services__slide-img {
        img {
          transform: scale(1.2);
        }
      }
    }
    &-img {
      overflow: hidden;
      img {
        width: 100%;
        border-radius: 0.1875em;
        transition: all 0.5s;
      }
    }
    &-desc {
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 1.5625em;
      transform: translate(-50%, 0);
      text-align: center;
      padding: 0 0.3125em;
    }
    &-logo {
      width: 2.5625em;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #000000;
    }
    &-price {
      font-weight: 600;
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #000000;
      margin-top: 0.3125em;
    }
  }
}
