    }
  }
  &__link {
    display: inline-block;
    font-size: 0.9375em;
    line-height: 120%;
    color: #000000;
    margin-top: 1.5625em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -0.125em;
      transform: translate(-50%, 0);
      width: 100%;
      height: 0.0625em;
      background: #000000;
    }
  }
}
.consultation {
  padding: 3.125em 0 6.25em;
  &__inner {
    text-align: center;
  }
  &__subtitle {
    font-size: 1.125em;
    line-height: 120%;
    color: #000000;
    margin-top: 0.9375em;
  }
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2.8125em;
  }
  &__item {
    width: 17.4375em;
    height: 4.25em;
    background: linear-gradient(180deg, #F2F2F2 0%, rgba(242, 242, 242, 0.56) 100%);
    border-radius: 0.375em;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-left: 1.5em;
    }
    &:hover {
      background: linear-gradient(180deg, rgba(242, 242, 242, 0.56) 100%, #F2F2F2 0%);
    }
    i {
      flex-shrink: 0;
    }
    span {
      font-weight: 300;
      font-size: 1.5em;
      line-height: 120%;
      letter-spacing: -0.04em;
      color: #000000;
      margin-left: 0.75em;
    }
  }
  &__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2em;
  }
  &__social {
    width: 32px;
    height: 32px;
    img {
      width: 100%;
      height: 100%;
    }
    &:not(:first-child) {
      margin-left: 0.5em;
    }
  }
}
.connection {
  margin-bottom: -4.375em;
  position: relative;
  z-index: 2;
  &__inner {
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    border: 0.0625em solid #EAEAEA;
    border-radius: 0.1875em;
    padding: 2.5em 2.5em 1.25em;
    max-width: 63.125em;
    margin: 0 auto;
  }
  &__left {
    width: calc(100% - 32.875em);
  }
  &__right {
    width: 31em;
    margin-left: 1.875em;
  }
  &__title {
    font-weight: 600;
    font-size: 1.5em;
    line-height: 120%;
    color: #000000;
  }
  &__subtitle {
    font-size: 1em;
    line-height: 120%;
    color: #000000;
    margin-top: 0.3125em;
  }
  &__form {
    &-group {
      height: 3.5em;
      display: flex;
      align-items: center;
      margin: 0;
      padding: 0;
      border: none;
      input {
        width: 19.5em;
        height: 100%;
        background: #F2F2F2;
        border-radius: 0.1875em 0px 0px 0.1875em;
        border: none;
        padding: 0 1.25em;
        font-size: 1.0625em;
        line-height: 120%;
        color: #000000;
      }
    }
    &-btn {
      width: 10.25em;
      height: 100%;
      background: #56BBF4;
      border-radius: 0px 0.1875em 0.1875em 0px;
      font-weight: 700;
      font-size: 1em;
      line-height: 120%;
      color: #FFFFFF;
    }
    &-check {
      display: flex;
      justify-content: center;
    }
  }
  &__check-wrapp {
    position: relative;
    margin-top: 0.5em;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      border: none;
      opacity: 0;
      cursor: pointer;
      &:checked ~ label:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 0.375em auto;
      }
    }
    label {
      display: inline-block;
      font-size: 0.8125em;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: rgba(0, 0, 0, 0.64);
      position: relative;
      padding-left: 17px;
      &:after {
        content: '';
        position: absolute;
        top: 0.3125em;
        left: 0;
        width: 0.6875em;
        height: 0.6875em;
        border-radius: 0.1875em;
        border: 0.0625em solid #000000;
        transition: all 0.5s;
      }
    }
  }
}

