.footer {
  position: relative;
  padding: 10em 0 5em;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 8em;
    background: linear-gradient(180deg, #F2F2F2 0%, rgba(242, 242, 242, 0) 100%);
  }
}
.footer-top {
  padding-bottom: 4.375em;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    max-width: 15em;
    margin-left: 3.75em;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 3.75em;
  }
  &__item {
    position: relative;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 0.75em;
      border-bottom: 0.065em solid #ededed;
      span {
        font-weight: 700;
        font-size: 0.9375em;
        line-height: 120%;
        color: #000000;
      }
      i {
        display: none;
        flex-shrink: 0;
      }
    }
    &-hidden {
      padding-top: 0.75em;
    }
    &-link {
      font-size: 0.9375em;
      line-height: 120%;
      color: #000000;
      &:not(:first-child) {
        margin-top: 0.9375em;
      }
    }
  }
  &__connection-item {
    width: 100%;
    height: 3.3125em;
    background: linear-gradient(180deg, #F2F2F2 0%, rgba(242, 242, 242, 0.56) 100%);
    border-radius: 0.375em;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-top: 0.3125em;
    }
    &:hover {
      background: linear-gradient(180deg, rgba(242, 242, 242, 0.56) 100%, #F2F2F2 0%);
    }
    i {
      flex-shrink: 0;
    }
    span {
      font-weight: 300;
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: -0.04em;
      color: #000000;
      margin-left: 0.75em;
    }
  }
  &__location {
    font-size: 0.9375em;
    line-height: 120%;
    color: #000000;
    margin-top: 0.75em;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 0.9375em;
  }
  &__social {
    width: 32px;
    height: 32px;
    img {
      width: 100%;
      height: 100%;
    }
    &:not(:first-child) {
      margin-left: 0.5em;
    }
  }
}
.footer-bottom {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 0.9375em;
    border-top: 0.0625em solid #ededed;
    position: relative;
  }
  &__center {
    text-align: center;
  }
  &__info {
    font-size: 0.75em;
    line-height: 120%;
    color: #000000;
  }
  &__logo {
    display: block;
    width: 8.4375em;
    margin: 0 auto;
    position: relative;
    margin-top: -2.8125em;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 15em;
      height: calc(100% + 1.25em);
      background: #ffffff;
    }
    img {
      width: 100%;
      position: relative;
      z-index: 1;
    }
  }
  &__clinic {
    font-size: 0.9375em;
    line-height: 120%;
    color: #000000;
    margin-top: 0.9375em;
    span {
      display: block;
      font-size: 0.75em;
      line-height: 105%;
      margin-top: 0.3125em;
    }
  }
  &__politic {
    font-size: 0.75em;
    line-height: 120%;
    text-align: right;
    color: #000000;
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    &__right {
      margin-left: 2.5em;
    }
    &__items {
      gap: 0.9375em;
    }
    &__item {
      &-show {
        span {
          font-size: 0.875em;
        }
      }
      &-link {
        font-size: 0.875em;
      }
    }
    &__connection-item {
      span {
        font-size: 1em;
      }
    }
    &__location {
      font-size: 0.8125em;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding-top: 18.125em;
  }
  .footer-top {
    padding-bottom: 2em;
    &__inner {
      flex-direction: column;
      max-width: 23.125em;
      margin: 0 auto;
    }
    &__right {
      max-width: 100%;
      margin: 1.5625em 0 0 0;
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      &-show {
        cursor: pointer;
        &--active {
          i {
            transform: rotate(180deg);
          }
        }
        i {
          display: block;
          transition: all 0.5s;
        }
      }
      &-hidden {
        display: none;
      }
    }
    &__location {
      text-align: center;
      br {
        display: none;
      }
    }
    &__socials {
      justify-content: center;
    }
  }
  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding-top: 0;
      border-top: unset;
      max-width: 23.125em;
      margin: 0 auto;
    }
    &__left {
      padding-bottom: 0.9375em;
      border-bottom: 0.0625em solid #ededed;
      order: -2;
    }
    &__center {
      margin-top: 1.5625em;
    }
    &__right {
      padding: 0.9375em 0;
      border-bottom: 0.0625em solid #ededed;
      order: -1;
    }
    &__info {
      br {
        display: none;
      }
    }
    &__logo {
      margin-top: 0;
      &:after {
        display: none;
      }
    }
    &__politic {
      text-align: center;
    }
  }
}